ul.products {
    > .product{
        > .yith-wcqv-button{
            display: none !important;
        }
    }
}

%yith-wcqv-button-style {
    .mkdf-yith-wcqv-holder {
        display: inline-block;
        vertical-align: middle;
        height: 40px;
        position: relative;

        .yith-wcqv-button {
            position: relative;
            display: inline-block;
            float: left;
            background-color: #fff;
            color: $second-main-color;
            font-size: 20px;
            width: 45px;
            height: 40px;
            line-height: 46px;
            @include mkdfTransition(background-color 0.3s ease-in-out);
            &:hover {
                background-color: $second-main-color;
                color: #fff;
            }

            div {
                background-size: 15px 15px !important;
            }
        }
    }
}

.mkdf-pl-holder .mkdf-pli-inner .mkdf-pli-text-inner,
.mkdf-plc-holder .mkdf-plc-item .mkdf-plc-text-inner,
ul.products > .product .mkdf-pl-inner .mkdf-pl-text-inner{
    @extend %yith-wcqv-button-style;
}