/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
    
    a.button,
    a.added_to_cart,
    button[type="submit"]:not(.mkdf-woo-search-widget-button),
    .wc-forward:not(.added_to_cart):not(.checkout-button) {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSolidColor();
		line-height: 14px;
        padding: 18px 20px;
        white-space: nowrap !important;
        cursor: pointer;
        z-index: 3;
        border: none;
        
        &:hover {
            @include mkdfButtonSolidHoverColor();
        }

       .mkdf-btn-icon-holder {
			margin-left: 21px;
			width: 53px;
			padding: 14px 0;
			text-align: center;
			line-height: 14px;
       }
    }



    a.button.added {
        display: none;
    }

    input[type="submit"] {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSolidColor();
        @include mkdfButtonSmallParams();
        z-index: 3;
        cursor: pointer;
        white-space: nowrap;
        border: none;
        &:hover {
            @include mkdfButtonSolidHoverColor();
        }
	}
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */

   