/* ==========================================================================
   WooCommerce Pagination styles - begin
   ========================================================================== */
.mkdf-woo-pagination-holder {
    @include mkdfRelativeHolderLayout();
    margin: 17px 0 60px;
    clear: both;

    .mkdf-woo-pagination-inner {
        position: relative;
        display: table;
        width: 100%;
        text-align: right;
    }
}

.woocommerce-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    text-align: center;
    width: 99%;
    margin: 0;
    padding: 0;

    .page-numbers {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        margin: 0;
        padding: 0;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0;
            

            a,
            span {
                position: relative;
                display: inline-block;
                width: auto;
                color: $first-main-color;
                font-family: $default-heading-font;
                font-size: 20px;
                margin: 0 7px;
                padding: 0;
                width: 44px;
                height: 44px;
                background-color: #fff0f4;
                border-radius: 50%;
                vertical-align: middle;
                line-height: 2.3em;
                text-decoration: none;
                cursor: pointer;
                white-space: nowrap;
                @include mkdfTransition(color .2s ease-out);


                &:hover,
                &.current {
                    color: #fff;
                    background-color: $first-main-color;

                }

            }

            a {

                &.next,
                &.prev {
                    font-size: 0;
                    line-height: 43px;
                    vertical-align: middle;
                    margin: 0 6px 0 4px;
                    font-family: 'Font Awesome\ 5 Free';
                    font-weight: 900;
                    text-rendering: auto;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;

                    &:before {
                        position: relative;
                        top: 0;
                        display: block;
                        font-size: 21px;
                        vertical-align: middle;
                        text-align: center;
                        left: 0;
                        right: 0;
                    }
                }
    
                &.prev {
        
                    &:before {
                        content: "\f053";
                    }
                }
    
                &.next {
        
                    &:before {
                        content: "\f054";
                    }
                }
            }

        }
    }
}

.mkdf-woo-view-all-pagination {
    position: relative;
    display: table-cell;
    vertical-align: top;
    width: 1%;
    display: none;

    a {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        white-space: nowrap;
        padding: 0 0 0 17px;
        color: $default-text-color;

        &:before {
            content: '';
            padding: 0 28px 0 0;
            border-left: 1px solid $default-text-color;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}
/* ==========================================================================
   WooCommerce Pagination styles - end
   ========================================================================== */