/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            padding: 10px 17px 0px;
            background-color: rgba($fourth-main-color, .1);
            @include mkdfAnimatedBorder($fourth-main-color);

            span {
                text-align: center;
                margin: auto;
                display: block;
                vertical-align: middle;
            }
            .mkdf-cart-title {
                position: relative;
                font-family: $default-heading-font;
                font-size: 14px;
                line-height: 26px;
                color: $default-heading-color;
            }

            &:hover {
                color: $default-heading-color;
            }
        }

        .mkdf-cart-number {
            position: absolute;
            top: 26px;
            right: 25px;
            background-color: $first-main-color;
            width: 15px;
            height: 15px;
            line-height: 1.4em;
            border-radius: 50%;
            text-align: center;
            font-size: 10px;
            color: #fff;
            font-weight: 600;
        }

        &.mkdf-header-cart-icon-pack {
            .mkdf-cart-icon {
                font-size: 30px;
                line-height: 1em;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            .mkdf-cart-icon {

                >* {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }

        &:hover {
            color: $default-heading-color;
            
            .mkdf-cart-icon {
                color: $default-heading-color;
            }
        }
    }

    &:hover {

		.mkdf-shopping-cart-dropdown {
			visibility: visible;
			max-height: 100vh;
			opacity: 1;
			@include mkdfTransition(max-height 0.6s cubic-bezier(.53,.01,.18,1), opacity 0s ease);
		}
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: -1px;
    width: 258px;
    background-color: #fff;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    text-align: left;
    z-index: 1000;
	max-height: 0;
	opacity: 0;
	visibility: hidden;
	overflow: hidden;
    box-sizing: border-box;
	@include mkdfTransition(opacity 0.2s, max-height 0s linear 0.15s);

    ul {
        margin: 21px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 0 0 15px;
            box-sizing: border-box;

            &.mkdf-empty-cart {
				font-size: 16px;
				line-height: 24px;
				font-family: $default-heading-font;
				color: $default-heading-color;
				border-bottom: 0;
            }

            &:first-child {
                padding-top: 0;
            }

			&:nth-last-child(2) {
				padding-bottom: 0;
			}

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 75px;
        vertical-align: top;

        img {
            display: block;
            border-radius: 5px;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        vertical-align: top;
        padding: 10px 16px;

        .mkdf-product-title {
            display: block;
            margin: 0;
            box-sizing: border-box;
            font-size: 16px;
            letter-spacing: 0.05em;
            
        }

        .remove {
            position: absolute;
            top: 8px;
            right: 0;
	        font-size: 18px;
	        line-height: 1;
            color: $first-main-color;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: inline-block;
            font-family: $default-text-font;
            font-size: 14px;
            letter-spacing: 0.05em;
            color: $default-text-color;
			margin: 3px 0 0;

            &:after {
                content: 'X';
                display: inline-block;
                position: relative;
                padding-left: 3px;
                color: $default-text-color;
            }
        }

        .amount {
            display: inline-block;
            margin: 0;
            color: $default-text-color;
            font-size: 14px;
        }
    }

    .mkdf-cart-bottom {
        padding: 10px 0 0;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 10px;

            > * {
                display: table-cell;
                vertical-align: top;
                color: $default-heading-color;
                line-height: 20px;
            }
            .mkdf-total{
                font-family: $default-heading-font;
                font-size: 16px;
            }

            .mkdf-total-amount {
                font-family: $default-heading-font;
                text-align: right;
	            font-size: 16px;
                font-weight: 500;
                color: $first-main-color;
            }
        }
        .mkdf-btn-holder:nth-child(2) {
            margin-right: 6px;
        }
        .mkdf-btn-holder {
            display: inline-block;
            float: left;
        }

        .mkdf-view-cart {
            @include mkdfRelativeHolderLayout();
            height: 40px;
            line-height: 40px;
            padding: 0 22px;
            font-family: $default-heading-font; 
            font-size: 14px;
            color: #fff;
            border-radius: 5px;
            background-color: $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include mkdfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $button-hover-color;
            }
        }
        .mkdf-btn-holder:nth-child(3) a {
           background-color: #7dd1ca;
           &:hover {
                background-color: #7ae0d8;
           }
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */