/* ==========================================================================
   Product List - Simple shortcode styles - begin
   ========================================================================== */

.mkdf-pls-holder {
	@include mkdfRelativeHolderLayout();

	> ul {
		@include mkdfRelativeHolderLayout();
		list-style: none;
		margin: 0;
		padding: 0;

		> li {
			@include mkdfRelativeHolderLayout();
			float: left;
			box-sizing: border-box;
			padding: 0;
			margin: 0 0 20px;

			&:last-child {
				margin: 0;
			}
		}
	}

	.mkdf-pls-image {
		display: table-cell;
		vertical-align: middle;
		width: 112px;

		a, img {
			display: block;
			border-radius: 10px;
		}

		a {
			position: relative;
			@include mkdfImageOverlayHoverStyle();
		}
	}

	.mkdf-pls-text {
		display: table-cell;
		vertical-align: middle;
		padding: 0 0 0 20px;

		.mkdf-pls-title {
			margin: 0 0 5px;
		}

		.mkdf-pls-rating-holder {
			@include mkdfRelativeHolderLayout();
			vertical-align: top;
			line-height: 1;
		}

		.mkdf-pls-rating {
			@include mkdfWooCommerceRatingsStyle();
			font-size: 15px;
		}

		.mkdf-pls-price {
			position: relative;
			display: block;
			font-family: $default-heading-font;
			font-size: 21px;
			line-height: 1em;
			letter-spacing: -0.01em;
			text-decoration: none;
			color: $first-main-color;
			margin: 3px 0 0;

			* {
				font-size: inherit;
				line-height: inherit;
			}
			del{
				color: $default-heading-color;
				margin: 0 4px 0 0;
			}
			ins{
				text-decoration: none;
			}

		}
	}

	$columns_space_label: ('no', 'tiny', 'small', 'normal', 'large');
	$columns_space_width: (0, 5, 10, 15, 25);

	@for $i from 0 to length($columns_space_label) {
		&.mkdf-#{nth($columns_space_label,$i+1)}-space {
			$column_width: nth($columns_space_width,$i+1);
			$column_width_padding: $column_width * 2;

			.mkdf-pls-inner {
				@if($column_width == 0) {
					margin: 0;
				} @else {
					margin: 0 -#{$column_width}px;
				}

				.mkdf-pls-item{
					padding: 0 #{$column_width}px;
					margin: 0 0 #{2*$column_width}px;
				}
			}
		}
	}
	$standard_list_columns: ('four', 'five', 'six');

	@for $i from 0 to length($standard_list_columns) {
		&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
			$column_width: 100%/($i+4);

			.mkdf-pls-item{
				width: $column_width;

				@media only screen and (min-width: $ipad-landscape-plus-pixel) {

					&:nth-child(#{$i+4}n+1) {
						clear: both;
					}
				}
			}
		}
	}

	$standard_list_columns: ('two', 'three');

	@for $i from 0 to length($standard_list_columns) {
		&.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
			$column_width: 100%/($i+2);

			.mkdf-pls-item{
				width: $column_width;

				@media only screen and (min-width: $ipad-portrait-plus-pixel) {

					&:nth-child(#{$i+2}n+1) {
						clear: both;
					}
				}
			}
		}
	}

}

/* ==========================================================================
   Product List - Simple shortcode styles - end
   ========================================================================== */