/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    background-color: $first-main-color;
    color: #32355d;
    font-family: $default-heading-font;
    font-size: 16px;
    letter-spacing: -0.01em;
    line-height: 23px;
    text-align: center;
    box-sizing: border-box;
    margin: 20px 0 0 20px;
    padding: 0 10px;
    z-index: 100;
    
}

@mixin mkdfWooCommerceSaleStyle() {
    left: 0;
    background: #fdbecf;
    border-color: transparent $first-main-color transparent transparent;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    left: 0;
    background-color: #b7eaf2;
    border-color: transparent $default-heading-color transparent transparent;
}

@mixin mkdfWooCommerceNewProductStyle() {
    left: 0;
    background-color: #dce086;
    border-color: transparent $default-heading-color transparent transparent;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    font-family: $default-heading-font;
    font-size: 21px;
    line-height: inherit;
    letter-spacing: -0.01em;
    text-decoration: none;
    margin: 2px 0 0;
    color: $first-main-color;
    
    * {
        font-size: inherit;
        line-height: 26px;
    }
    
    del {
        font-family: $default-heading-font;
        font-size: 21px;
        line-height: 26px;
        letter-spacing: -0.01em;
        margin: 0 5px 0 0;
        color: $default-heading-color;
    }
    
    ins {
        font-family: $default-heading-font;
        font-size: 21px;
        line-height: 26px;
        letter-spacing: -0.01em;
        text-decoration: none;
        color: $first-main-color;
    }
}
/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "ElegantIcons";
    font-size: 12px;
    line-height: 1;
    letter-spacing: 5px;
    color: $third-main-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\e031\e031\e031\e031\e031";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
		font-size: 0;
        overflow: hidden;
        
        &:before {
            content: "\e033\e033\e033\e033\e033";
			font-size: 15px;
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\e031";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\e033\e033\e033\e033\e033";
        position: absolute;
        right: 0;
        bottom: 7px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */