/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {

    .mkdf-quantity-buttons {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        border: 1px solid #eee;
        border-radius: 10px;
        background-color: #f8f8f8;
        box-sizing: border-box;
        line-height: 44px;
        height: 50px;
        max-width: 202px;
        width: 100%;
        padding: 0 10px 0 20px;
        margin: 0 -0.3em 0 0;
       
        .mkdf-quantity-label {
            font-family: $default-text-font;
            font-size: 14px;
            margin-right: 43px;
            display: inline-block;
            vertical-align: middle;
            color: $default-form-color;
            background-color: transparent;
            text-align: center;
            border: 0;
            box-sizing: border-box;

        }

        span{
            &.mkdf-quantity-plus,
            &.mkdf-quantity-minus{
                color:  $default-form-color;
                font-size: 18px;
            }
        }
        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            display: inline-block;
            vertical-align: middle;
            color: $default-text-color;
            background-color: transparent;
            text-align: center;
            box-sizing: border-box;
        }

        .mkdf-quantity-input {
            font-style: normal;
            font-family: inherit;
            font-size: 14px;
            color: $default-form-color;
            border: 1px solid transparent;
            position: relative;
            width: 20px;
            padding: 0;
            margin: 0;
            outline: 0;
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            height: 22px;
            line-height: 22px;
            border-left: 0;
            cursor: pointer;
            @include mkdfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {

    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        position: relative;
        width: 100%;
        margin: 0 0 19px;
        padding: 19px 20px;
        font-family: inherit;
        font-size: 12px;
        line-height: 18px;
        font-weight: inherit;
        color: $default-text-color;
        background-color: transparent;
        border: 1px solid $default-border-color;
        border-radius: 10px;
        outline: 0;
        -webkit-appearance: none;
        box-sizing: border-box;

      
        @include mkdfPlaceholder {
            color: inherit;
        }
    }

    .coupon {

        input[type="text"] {
            width: 200px;
            border-radius: 5px;
        }
    }
}
/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */