/* ==========================================================================
   YITH Quick View styles - begin
   ========================================================================== */
#yith-quick-view-modal,
.yith-quick-view.yith-modal {

    .yith-quick-view-overlay {
        background-color: rgba(12, 12, 18, 0.65);
    }

    .yith-wcqv-wrapper {
        padding: 0;
        max-width: 1086px;
        width: 100% !important;
        height: auto !important;
    }

    .yith-wcqv-main {
	    @include mkdfRelativeHolderLayout();
	    padding: 0;
	    overflow: hidden !important;
	    box-shadow: none;
        border-radius: 15px;
    }

    #yith-quick-view-content {
        .product{
            margin: 0;
            .mkdf-social-title {
                font-family: $default-heading-font;
                font-size: 16px;
                vertical-align: middle;
                margin-right: 10px;
            }
            .mkdf-quick-view-gallery {
                .owl-prev,
                .owl-next {
                    width: 58px;
                    height: 58px;
                    background-color: #fff;
                    border-radius: 50%;

                    .mkdf-prev-icon {
						position: relative;
						display: block;
                        text-align: center;
						font-size: 28px;
                        line-height: 2.3em;
                        color: $first-main-color;
                    }
                    .mkdf-next-icon {
                        position: relative;
                        display: block;
                        text-align: center;
                        line-height: 2.3em;
						font-size: 28px;
                        color: $second-main-color;
                    }
                }
                .owl-prev {
                    left: 20px;
                }
                .owl-next {
                    right: 20px;
                }
            }
        }

        .product-type-grouped {
            .summary {
                p.price {
                    margin: 12px 0 0;
                }
                .mkdf-woo-social-share-holder,
                .yith-wcwl-add-to-wishlist {
                    margin-top: 0;
                }
                .cart {
                    margin: 0px 0 28px;
                }
                .woocommerce-product-details__short-description {
                    margin: 0 0 20px;

                }
            }
        }

        .product-type-external {
            .summary {
                .mkdf-single-product-share-wish {
                    margin-top: 47px;
                }
            }
        }

        .product-type-variable {

            .summary {
                p.price {
                    margin: 13px 0 0;
                }
                 .woocommerce-product-details__short-description {
                    margin: 0 0 22px;

                }
            }

            form {
                .select2-container--default{
                    float: none;
                }

                .single_variation_wrap {
                    margin: 5px 0 0;
                }
            }
            .summary {
                form.cart {
                    margin: 0;
                }
            }
        }

        .images,
        .summary {
            position: relative;
            float: left;
            box-sizing: border-box;
        }

        .images {
			width: 45%;

            a, img {
                display: block;
            }
        }

        .summary {
			width: 55%;
            padding: 50px 43px 30px;
            position: relative;
            min-height: 544px;

            .product_title {
                font-size: 40px;
            }
            .mkdf-quantity-buttons {
                margin: 0 10px 0 0;

            }
            .single_add_to_cart_button {
                margin: 0;
            }

            .variations {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0 0 20px;
                border: none;
                padding: 0;

                select {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    background-color: #fff;
                    border: 1px solid #e1e1e1;
                    box-sizing: border-box;
                    line-height: 50px;
                    height: 50px;
                    padding: 0 16px 0 16px;
                    margin: 0 -4px 0 0;
                    color: $default-text-color;
                    font-weight: 300;
                    font-size: 13px;
                    font-family: $default-text-font;
                }

                tr {
                    border: 0;
                    display: block;
                    float: left;

                    &:first-child {
                        padding-right: 6px;
                    }
                }

                td {
                    padding: 0;
                    text-align: left;
                    display: block;
                }

                td.label{
                    width: 12%;
                }

                .select2-container{
                    min-width: 180px;
                }

                .reset_variations {
                    color: $default-heading-color;
                    position: relative;
                    top: -5px;
                    right: -10px;
                }
            }

            .single_variation_wrap {
                margin: 35px 0 0;

                .single_variation {
                    margin: 0 0 35px;
                }
            }

            table.group_table {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin: 0;
                border: none;
                padding: 0;

                tbody {
                    display: block;
                }

                tr {
                    border: 1px solid transparent;
                    display: table;
                    margin-bottom: 13px;
                    width: 100%;
                    background-color: #fff;
                }

                td {
                    box-sizing: border-box;
                    padding: 0 10px;
                    text-align: left;
                    display: inline-block;
                    &.woocommerce-grouped-product-list-item__quantity {
                        width: 21%;
                    }

                    &:first-child {
                        padding: 0;
                    }

                    &.label {
                        text-align: left;
                        vertical-align: middle;
                    }

                    &.price {
                        display: table-cell;
                        font-size: 16px;
                        margin: 0;
                        text-align: center;
                        vertical-align: middle;

                    }
                }

                .mkdf-quantity-buttons {
                    
                    padding: 0px 10px;

                    .mkdf-quantity-label{
                        display: none;
                    }

                    .mkdf-quantity-input{
                        width: 20px;
                        margin: 0;
                    }
                }

                a {
                    color: $default-text-color;

                    &:hover {
                        color: $default-heading-color;
                    }
                }
            }

            .mkdf-yith-product-title {
                display: block;
                margin: 0;
            }

            div[itemprop="offers"] {
                position: relative;
                display: block;
            }

            .price {
                position: relative;
                display: block;
                margin: 6px 0 10px;
                line-height: 1;
                font-family: $default-heading-font;
                font-size: 26px;
                color: $first-main-color;

                * {
                    font-size: inherit;
                    line-height: inherit;
                }

                del {
                    margin: 0 8px 0 0;
                    font-size: 21px;
                    color: $default-heading-color;
                }

                ins {
                    text-decoration: none;
                }
            }
	
	        .woocommerce-product-rating {
		        @include mkdfRelativeHolderLayout();
		        line-height: 1;
		        margin: 5px 0 36px;
	        }

            div[itemprop="description"],
            .woocommerce-product-details__short-description{
                @include mkdfRelativeHolderLayout();
	            margin: 0 0 22px;

                p {
                    margin: 0;
                }
            }

            form.cart {
                @include mkdfRelativeHolderLayout();
                margin: 0 0 30px;
            }

            .mkdf-single-product-share-wish {
                position: relative;
                display: inline-block;
                width: 100%;
                vertical-align: middle;
                margin: 0px;

                .yith-wcwl-wishlistexistsbrowse,
                .yith-wcwl-wishlistaddedbrowse {
                    a {
                        &:after {
                            color: $first-main-color;
                        }
                    }
                }

                .clear{
                    display: none;
                }

                > div{
                    padding: 0 20px;

                    &:after{
                        content: "|";
                        color: #d3d3d3;
                        position: absolute;
                        left: 0;
                        top: 0;
                    }

                    &:first-child{
                        padding-left: 0;

                        &:after{
                            display: none;
                        }
                    }

                    &:last-child{
                        padding-right: 0;
                    }
                }

                .mkdf-woo-social-share-holder {
                    float: left;
                    position: relative;
                    display: inline-block;

                    > * {
                        display: table-cell;
                        vertical-align: top;
                        line-height: 22px;
                    }

                    > span {
                        font-size: 12px;
                        text-transform: uppercase;
                        letter-spacing: 2px;
                        color: $first-main-color;
                        padding: 0 9px 0 0;
                    }
                }
            }
            .clear {
                display: none;
            }
            .mkdf-woo-social-share-holder {
                margin-top: 105px;
                display: inline-block;
            }
            .yith-wcwl-add-to-wishlist {
                width: auto;
                background-color: transparent;
                float: left;
                padding-top: 4px;
                margin-top: 105px;

                .yith-wcwl-add-button,
                .yith-wcwl-wishlistaddedbrowse,
                .yith-wcwl-wishlistexistsbrowse {
                    a { 
                        font-family: $default-heading-font;
                        font-size: 16px;
                        line-height: inherit;
                        color: $first-main-color;
                        line-height: 22px;
                        padding: 0 15px 0 0;
                        border-right: 1px solid #d3d3d3;
                        margin-right: 13px;
                        border-radius: 0px;

                        &:after {
                            content: "\53";
                            font-size: 16px;
                            float: left;
                            padding-right: 15px;
                            line-height: 21px;
                        }
                    }
                }

                .yith-wcwl-add-button {
                    a {
                        &:after {
                            content: "\e004";
                        }
                    }
                }
            }

            p.stock.out-of-stock,
            p.stock.in-stock {
                color: $default-heading-color;
                font-weight: bold;
                margin: 0 0 18px;
            }
        }
    }

    #yith-quick-view-close {
        top: 18px;
        right: 20px;
        width: 30px;
        height: 30px;
        font-size: 0;
        line-height: 30px;
	    font-weight: 400;
        text-align: center;
        opacity: 1;
        border: 0;
        color: $first-main-color;

        &:before {
            position: relative;
            display: inline-block;
            vertical-align: top;
            font-family: 'ElegantIcons';
            content: "\4d";
	        font-size: 30px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        &:hover {
            opacity: 0.8;
        }
    }
}
/* ==========================================================================
   YITH Quick View  styles - end
   ========================================================================== */