/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {

				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 140px;
							width: calc(100% - 140px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 118px;
								height: calc(25% + 3px);
								padding: 0 0 12px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;

									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (3 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}

			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 30px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 66px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 50%;
		    margin: 0;
		    padding: 0 32px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
					    border-radius: 5px;
				    }
			    }
				.woocommerce-product-gallery__image:first-child {
				 	img {
				 		border-radius: 10px;
				 	}
			 	
				}
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 50%;
        padding: 0 0 0 32px;
        float: left;
        box-sizing: border-box;

        .mkdf-single-product-title {
        	margin: 0;
        }

	
	    .price {
		    @include mkdfWooCommercePriceStyle();
		    font-size: 26px;
			ins{
				font-size: 22px;
				font-weight: 400;
			}
			* {
				font-size: 26px;
			}
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 0;
		    margin: 6px 0 45px;
			.star-rating{
				font-size: 15px;
				color: $third-main-color;
			}
			.woocommerce-review-link {
				display: none;
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 17px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 37px 0 0;
		
		    > span {
			    display: block;
		        margin: 0 0 7px 0;
				color: $default-heading-color;
				font-weight: 400;
				font-size: 14px;
			
				&.sku_wrapper {
					text-transform: uppercase;
				}
				span, a{
					color: $default-text-color;
					font-size: 13px;
					font-weight: 400;
					padding-left: 5px;
				}
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
			margin: 10px 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		    > span {
			    padding: 0 9px 0 0;
				color: $default-heading-color;
				font-weight: 700;
				font-size: 13px;
				letter-spacing: 0.05em;
				text-transform: uppercase;
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $second-main-color;
		    font-weight: bold;
		    margin: 22px 0 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 22px 0 0;
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 6px 0 0;

			.mkdf-quantity-input{
				font-style: normal;
				color: $default-form-color;
				font-size: 14px;
				position: relative;
				width: 20px;
	            padding: 0;
	            margin: 0;
	            outline: 0;
			}
			 
			span{
				&.mkdf-quantity-plus,
				&.mkdf-quantity-minus{
					color:  $default-form-color;
					font-size: 18px;
				}
			}
	    }


    }

    .yith-wcwl-add-to-wishlist .yith-wcwl-add-button,
    .yith-wcwl-add-to-wishlist .yith-wcwl-wishlistaddedbrowse {

    	&:hover {
    		a {
    			color: $first-main-color;
    		}

    	}
    }
   .related {
    	.yith-wcwl-add-to-wishlist .yith-wcwl-add-button,
	    .yith-wcwl-add-to-wishlist .yith-wcwl-wishlistaddedbrowse {

	    	&:hover {
	    		a {
	    			color: #fff;
	    		}

	    	}
	    }

    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 30px 0 0;
		
		
		ul.tabs {
			@include mkdfRelativeHolderLayout();
			display: block;
			vertical-align: middle;
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: center;
			font-size: 0;
			text-align: left;
			clear: both;
			bottom: -1px;
			z-index: 9999;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
			    border: 1px solid #d7f5fa;
				&.active{
					
					border-bottom: 1px solid #fff;
					a{
						color: $first-main-color;
					    background-color: transparent;
					}
				}
				
				a {
					position: relative;
					display: inline-block;
					box-sizing: border-box;
					vertical-align: top;
				    padding: 23px 40px 23px;
				    font-family: $default-heading-font;
					font-size: 18px;
					font-weight: 400;
					line-height: 26px;
					text-transform: capitalize;
					background-color: #f7fdfe;
					color: #32355d;
				}
				&:last-child {
					margin: 0;
				}
			}
			li:first-child {
				border-right: 1px solid transparent;
				border-top-left-radius: 5px;
			}
			li:last-child {
				border-top-right-radius: 5px;
				// border-left: 1px solid transparent;
			}
			li.additional_information_tab {
				border-right: 1px solid transparent;
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			box-sizing: border-box;
		    width: 100%;
		    padding: 55px 40px 59px;
		    border: 1px solid #d7f5fa;
		    border-top-right-radius: 5px;
		    border-bottom-right-radius: 5px;
		    border-bottom-left-radius: 5px;
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 17px 0 0;
			}
			
		}
		
		table {
			margin: 0;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				width: 1%;
				font-size: 14px;
				font-weight: 400;
				padding: 0 10px 0 0;
			}
			
			td {
				width: 99%;
				font-size: 13px;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			padding: 0 0 5px;
			box-sizing: border-box;
			
			h2 {
				font-size: 24px;
				line-height: 27px;
				margin: 5px 0 22px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
						padding-top: 10px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 100px;
					
					p {
						margin: 0;
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}

				textarea{
					height: 135px;
				}

				textarea,
				input[type=text],
				input[type=email] {
				    border: 1px solid #eee;
				    background-color: #f8f8f8;
				    color: #9f9e9e;
				}
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 50px 0 0;
		
		> h2 {
			display: block;
			margin: 35px 0;
			font-size: 30px;
   			line-height: 1.4em;
			text-align: left;
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
			.star-rating{
				color: $third-main-color;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */