/* ==========================================================================
   WooCommerce Variation styles - begin
   ========================================================================== */

.mkdf-woocommerce-page .mkdf-content {
    
    .variations {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;
        
        tbody {
            display: block;
            widows: 100%;
        }
        
        tr {
            display: inline-block;
            // width: 36%;
            margin-right: 12px;
            border: 1px solid transparent;
            
            &:last-child {
                margin: 0;
            }
        }
        
        td {
            position: relative;
            display: block;
            font-family: $default-text-font;
            font-size: 14px;
            text-transform: none;
            text-align: initial;
            color: $default-form-color;
            padding: 0;
            
            
            &.value {
                white-space: nowrap;
                
                select {
                    width: auto;
                    min-width: 193px;
                }
            }
        }
        
        .reset_variations {
            display: none !important; // Push selection button to the right side, when reset varition field is visible , that's not possible. 
            margin: 0 0 0 15px;
            color: $default-heading-color;
        }

        .select2-container--default {
            text-align: left;
            width: auto;
            min-width: 193px;
        }
        .select2-dropdown {
            left: -1px;
        }
    }
    
    .single_variation_wrap {
        margin: 19px 0 0;
        
        .single_variation {
            margin: 0 0 35px;
        }
        .variations_button {
            button {
                .mkdf-btn-icon-holder {
                    display: none;
                }
            }
        }
    }
    
    table.group_table {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin: 0;
        border: none;
        padding: 0;
        
        tbody {
            display: block;
        }
        
        tr {
            display: block;
            padding: 0 0 8px;
            border: 0;
			margin-bottom: 0;
            
            &:last-child {
                margin: 0;
            }
        }
        
        td {
            padding: 0 17px 10px 0;
            text-align: left;
            
            &.price {
                display: table-cell;
                vertical-align: middle;
            }
            
            &:last-child {
                padding: 0 0 10px;
            }
        }
        
        .mkdf-quantity-buttons {
            margin: 0;
        }
        
        a {
            color: $default-text-color;
            
            &:hover {
                color: $default-heading-color;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Variation styles - end
   ========================================================================== */