/* ==========================================================================
Product Categories shortcode styles - begin
========================================================================== */

.mkdf-product-categories-holder{

    .mkdf-product-category{
        float: left;
        position: relative;
        box-sizing: border-box;
        @include mkdfTransition(opacity .2 ease-in-out);

        .mkdf-product-category-inner{
            @include mkdfRelativeHolderLayout();
            height: 100%;
			padding: 28px 0 22px;
			background-color: rgba($third-main-color, 0.1);
			border-radius: 10px;
            box-sizing: border-box;
            @include mkdfAnimatedBorder($third-main-color);
			@include mkdfTransition(padding 0.2s ease-in-out);

			.mkdf-product-category-img-wrapper{
				display: inline-block;
			}

			.mkdf-link{
				@include mkdfAbsoluteHolderLayout();
			}
        }

        .mkdf-product-category-content{
           &.top{
               position: absolute;
               top:0;
               left:0;
           }

           &.bottom{
               position: relative;
               margin: 24px 0 0 0;
               text-align: center;
            }

            .mkdf-category-title{
                margin: 10px 0 0;
            }
        }

        &.mkdf-category-with-image{
        	text-align: center;
        	img{
        		display: block;
        	}

            .mkdf-product-category-content{
                &.top{
                    top: 23px;
                    left: 25px;
                }
            }
        }
		&:nth-child(3n + 2){
			.mkdf-product-category-inner{
				background-color: rgba($first-main-color, 0.05);
				@include mkdfAnimatedBorder(rgba($first-main-color, .4));
			}
		}
		&:nth-child(3n + 3){
			.mkdf-product-category-inner{
				background-color: rgba($second-main-color, 0.1);
				@include mkdfAnimatedBorder($second-main-color);
			}
		}

    }
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'large');
$columns_space_width: (0, 5, 10, 15, 25);

@for $i from 0 to length($columns_space_label) {
&.mkdf-#{nth($columns_space_label,$i+1)}-space {
    $column_width: nth($columns_space_width,$i+1);
    $column_width_padding: $column_width * 2;

    .mkdf-product-categories-holder-inner {
        @if($column_width == 0) {
            margin: 0;
            } @else {
                margin: 0 -#{$column_width}px;
            }

            .mkdf-product-category{
                padding: 0 #{$column_width}px;
                margin: 0 0 #{2*$column_width}px;
            }
        }
    }
}

/*************** Standard Layout - begin ***************/

$standard_list_columns: ('two', 'three', 'four', 'five', 'six');

@for $i from 0 to length($standard_list_columns) {
    &.mkdf-#{nth($standard_list_columns,$i+1)}-columns {
        $column_width: 100%/($i+2);

        .mkdf-product-category{
            width: $column_width;

            @media only screen and (min-width: $phone-landscape-plus-pixel) {

                &:nth-child(#{$i+2}n+1) {
                    clear: both;
                }
            }
        }
    }
}

/*************** Standard Layout - end ***************/
