/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
    position: absolute; //absolute centering over added item
    margin:0 !important; //absolute centering over added item
    -webkit-transform-origin:0 0;
    transform-origin:0 0;
    @include mkdfTransform(translate(-50%,-50%));
    border: 1px solid #ebebeb;
    border-radius: 0;
    padding: 0 21px;
    line-height: 35px;

    #yith-wcwl-message {
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 0.1em;

        &:before {
            position: relative;
            font-family: 'ElegantIcons';
            content: "\4e";
            top: 2px;
            right: 7px;
        }
    }
 
    //vanishing effect
    &.mkdf-wishlist-vanish-out {
      @include mkdfAnimation(mkdfWishlistvanishOut 1s both);
    }

    @keyframes mkdfWishlistvanishOut {
      0% {
        opacity: 1;
        @include mkdfTransform(scale(1,1) translate(-50%,-50%));
        -webkit-filter: blur(0px);
        filter: blur(0px);
      }

      100% {
        opacity: 0;
        @include mkdfTransform(scale(2,2) translate(-50%,-50%));
        -webkit-filter: blur(10px);
        filter: blur(10px);
      }
    }
}

.mkdf-yith-wcqv-holder {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0 2px 0 0;
    background-color: #fff;
    width: 45px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    @include mkdfTransition(background-color 0.3s ease-in-out);
    &:hover {
        background-color: $first-main-color;
       
    }
   
}
.yith-wcwl-wishlistexistsbrowse:hover a {
   
            color: #fff;
     
   
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        position: relative;
        display: inline-block;
        vertical-align: top;
        font-size: 0;
        line-height: inherit;
        color: $first-main-color;

        &:after {
            content: "\e004";
            position: relative;
            display: inline-block;
            top: 0;
            vertical-align: top;
            font-family: "dripicons-v2" !important;
            font-size: 20px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            @include mkdfTransition(opacity .2s);
            &:hover {
                @include mkdfTransition(opacity .2s);
            }
        }

        // content: "\4e";

        &:not(.mkdf-adding-to-wishlist):hover {
            &:after {
                opacity: .8;
            }
        }

        &.mkdf-adding-to-wishlist {
            @include keyframes(mkdfWishlistLoading) {
                0%, 100% {
                    opacity:1;
                }

                50% {
                    opacity: 0.2;
                }
            }

            &:after {
                @include mkdfAnimation(mkdfWishlistLoading 1s ease infinite);
            }
        }

        
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        &:after {
            content: "\53";
        }
    }
}

.yith-wcwl-add-button {
    .ajax-loading {
        display: none !important;
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    span {
        display: none;
    }

}

.woocommerce-wishlist {

    .wishlist-title{
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 15px 30px;
        margin: 50px 0 30px;
        line-height: 48px;
        list-style: none;
        background-color: transparent;
        border: 1px solid $default-border-color;

        > a {
            color: $default-text-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 38px 0 90px;

        thead {
            display: none;
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 1px solid #f1f1f1;

                td {
                    padding: 22px 0;
                    vertical-align: middle;
                    text-align: center;

                    &.product-remove {

                        a {
                            color: $default-text-color;
                            font-size: 0;

                            &:after {
                                content: "\f404";
                                display: inline-block;
                                vertical-align: middle;
                                speak: none;
                                color: inherit;
                                font-family: "Ionicons";
                                font-size: 25px;
                                font-style: normal;
                                font-weight: normal;
                                font-variant: normal;
                                text-transform: none;
                                text-rendering: auto;
                                line-height: 33px;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                            &:hover {
                                color: $default-heading-color;
                            }
                        }
                    }

                    &.product-thumbnail {
                        padding-left: 8px;
                        padding-right: 8px;
                        width: 92px;

                        a, img {
                            display: block;
                        }
                    }

                    &.product-name {
                        text-align: left;
                        padding-left: 20px;

                        a {
							color: $default-text-color;

							&:hover{
								color: $first-main-color;
							}
                        }

                        .yith-wcqv-button {
                            height: 38px;
                            line-height: 38px;
                            padding: 0 28px;
                            margin: 0;
                        }
                    }

                    &.product-price{

                        ins{
                            text-decoration: none;
                        }
                    }

                    &.product-stock-status span{
                        color: $default-text-color !important;
                    }

                    &.product-add-to-cart {

                        a {
                            display: inline-block !important;
                            background-color: $first-main-color;
                            box-sizing: border-box;
                            border: 0;
                            border-radius: 5px;
                            padding: 0 19px;
                            margin: 0;
                            font-family: $default-heading-font;
                            font-size: 20px;
                            color: #fff;
                            cursor: pointer;
                            white-space: nowrap!important;
                            line-height: 50px;
                            height: 50px;

                            &:hover{
                                background: transparent;
                                color: inherit;
                            }
                        }
                    }
                }
            }
        }

        .yith-wcwl-share{
            margin: 0;
        }
    }
}

.mkdf-single-product-summary {
    .yith-wcwl-add-to-wishlist {
        width: auto;
        background-color: transparent;
        float: left;
        margin-top: 35px;

        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {
            a { 
                font-family: $default-heading-font;
                font-size: 14px;
                line-height: inherit;
                color: $first-main-color;
                text-transform: uppercase;
                letter-spacing: 2px;
                line-height: 20px;

                &:hover {
                    opacity: 0.9;
                    @include mkdfTransition(opacity .2s ease-in-out);
                }

                &:after {
               
                    font-size: 12px;
                    float: left;
                    padding-right: 10px;
                    line-height: 19px;
                    color: $first-main-color;
                }
            }
        }

        .yith-wcwl-add-button {
            a {
                &:after {
                    content: "\53";
                }
            }
        }
    }
}

.mkdf-wishlist-widget-holder{
    display: inline-block;
    vertical-align: middle;
    margin: 0 0 0 5px;

    a {
        color: $default-heading-color;
        font-size: 13px;
        margin: 0 10px;

        .mkdf-wishlist-items-number {
            color: $default-text-color;
            font-family: $default-text-font;
            font-size: 12px;
        }

        .mkdf-wishlist-widget-icon {
            display: inline-block;
            position: relative;
            font-size: 30px;
            text-align: center;
            padding: 8px 14px 0;
            background-color: rgba($third-main-color,.1);
            @include mkdfAnimatedBorder($third-main-color);

            i { 
                @include mkdfTransition(color .2s ease-in-out);
                &:hover {
                    color: $default-heading-color;
                }
            }
            
        }
        .mkdf-wishlist-title {
            display: block;
            position: relative;
            font-family: $default-heading-font;
            font-size: 14px;
            text-align: center;
        }

        &:hover {
            color: $default-heading-color;
        }
    }
}

.mkdf-light-header {
    .mkdf-wishlist-widget-holder{
        a {
            color: $header-light-color;
            .mkdf-wishlist-items-number {
                color: $header-light-color;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

.mkdf-dark-header {
    .mkdf-wishlist-widget-holder{
        a {
            color: $header-dark-color;
            .mkdf-wishlist-items-number {
                color: $header-dark-color;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}
.yith-wcwl-add-to-wishlist {
    .yith-wcwl-add-button,
    .yith-wcwl-wishlistaddedbrowse {
        &:hover {
            a {
                color: #fff;
            }
        }
    }
}


/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */